declare var $:any 
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpServicesProvider } from '../../../providers/http-services';
import { Utils } from '../../../providers/Utils';
import { StorageProvider } from '../../../providers/storage';
import { PhoneDeviceProvider } from '../../../providers/phone-device';

/**
 * Generated class for the LmpjMeetingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmpjMeeting',
  templateUrl: 'lmpjMeeting.html',
})
export class LmpjMeetingPage {
  public tabNameList = [{'name':'我的','code':'','value':''},{'name':'立项','code':'','value':''},{'name':'策划','code':'','value':''},{'name':'准备','code':'','value':''},{'name':'执行','code':'','value':''},{'name':'验收','code':'','value':''}]
  public list=[];//列表
  public dataList = []; //缓存页码数据（上拉加载）
	public mCurrentPage = 0;//默认page
  public where='';//搜索
  public description='';//搜索输入框内容
  public searchlist = [];
  public htRegistrationType=[];//隐患类型
  public stops='';//无数据加载完毕
  public currentTab = 0;//tab默认第一个
  public itemTab=99;
  public status ='';
  public images=[];
  public htIllegallist = [];
  public projectid = '';
  constructor(public navCtrl: NavController,public storage: StorageProvider, public navParams: NavParams,public httpService: HttpServicesProvider,
    public phoneDevice:PhoneDeviceProvider) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);
    this.projectid = this.storage.get(StorageProvider.LM_PROJECTID);
    
  }
  ionViewWillEnter(){//当将要进入页面时触发
    this.list =[];
    this.htIllegallist = [];
    this.initlist(1);
  }
  searchClick(){//搜索
    $('.searchWarp').toggle();
    $('.searchWarp').css({'height':'auto'});
    var display =$('.searchWarp').css('display');
    if(display == 'block'){
      document.getElementById('topest').scrollIntoView();
    }
    this.description = '';
    this.searchlist = [];
  }
  search(){//点击搜索
    if(this.description != ''){
    var fuzzysearch =   encodeURIComponent(this.description);
    var url = '/lmpjProject/list?projectid='+this.projectid+'&rows=-1&page=1&sidx=&sord=asc&fuzzysearch='+fuzzysearch;
      // $('.searchWarp').css({'height':'100%'}); 
      this.initsearchlist(url)
    }
  }
  searchBack(){//点击取消搜索
    this.description = '';
    this.searchlist = [];
    $('.searchWarp').hide();
    $('.tab-nav').show();
    this.list = [];
    this.initlist(this.mCurrentPage)
  }
  clickInfo(id){//查看详情
    this.description = '';
    this.searchlist = [];
    this.list = [];
    $('.searchWarp').hide();
    $('.tab-nav').show();
    this.initlist(this.mCurrentPage)
    this.navCtrl.push('LmpjMeetinginfoPage',{
      type:'edit',
      id:id
    })
  }
  showscanner() {
    this.navCtrl.push('ScannerPage', {'type':'piinout'}, { "animate": false });
   
  }
  initsearchlist(url){//初始化搜索列表
    var that = this;
    $('.tab-nav').hide();
    that.httpService.get(url, null, (result) => {
      if (result != null && result.rows.length > 0) {
        var rows = result.rows;
        that.list = rows;
       
      }else{
        $('.searchWarp .listWarp').html('没有搜索到相关数据').css({'text-align':'center'});
      }
    });
  }
  initlist(page){//初始化列表
    var url = '';
    console.info(this.currentTab);
    if(this.currentTab == 0){
      url = '/lmpjMeeting/list?projectid='+this.projectid+'&sort=meetcode&order=desc&_search=false&rows=20&page='+page+'&sidx=&sord=asc&nw_resperson1=my'
    }
    if(this.currentTab == 1){
      url = '/lmpjMeeting/list?projectid='+this.projectid+'&sort=meetcode&order=desc&_search=false&rows=20&page='+page+'&sidx=&sord=asc&nw_status=1'

    }
    if(this.currentTab == 2){
      url = '/lmpjMeeting/list?projectid='+this.projectid+'&sort=meetcode&order=desc&_search=false&rows=20&page='+page+'&sidx=&sord=asc&nw_status=2'

    }
    if(this.currentTab == 3){
      url = '/lmpjMeeting/list?projectid='+this.projectid+'&sort=meetcode&order=desc&_search=false&rows=20&page='+page+'&sidx=&sord=asc&nw_status=3'
    }
    if(this.currentTab == 4){
      url = '/lmpjMeeting/list?projectid='+this.projectid+'&sort=meetcode&order=desc&_search=false&rows=20&page='+page+'&sidx=&sord=asc&nw_status=4'
    }
    var that = this;
    var lists = this.list;
    that.httpService.get(url, null, (result) => {
        if(page > result.total){
          this.stops = 'true';
          return false;
        }
      if (result != null && result.rows.length > 0) {
        var rows = result.rows;
        for(var i=0;i<rows.length;i++){
          if(rows[i].planfinishtime != '' && rows[i].planfinishtime != null){
            rows[i].planfinishtime = Utils.dateFormat(new Date(rows[i].planfinishtime ), 'yyyy-MM-dd');
          }
          if(rows[i].checktime != '' && rows[i].checktime != null){
            rows[i].checktime = Utils.dateFormat(new Date(rows[i].checktime ), 'yyyy-MM-dd hh:mm');
          }
        }
          that.dataList = rows;
          lists = lists.concat( that.dataList);
          that.list = lists;
          that.mCurrentPage = page;
      }
    });
  }
  chooseStatus(value,index){
    $('.tab-nav .nav-item:eq('+index+')').addClass('active').siblings().removeClass('active');
    $('.screenBox .item').removeClass('active');
    // let url = '';
   
    // /piInOut/list?sort=num&order=desc&sigman=
    // let obj={"nw_wfstatus":value};
    // var where = '';
    if (this.currentTab == index) {
      return false;
    } else {
      this.currentTab = index;
    }
   
    // where = encodeURIComponent(JSON.stringify(obj));
    // this.where = where;
    // this.status = value;
    this.list = [];
    // this.htIllegallist = [];
    this.initlist(1)
  }
  
  doRefresh(refresher) {//下拉刷新
    this.list =[];
    this.htIllegallist = [];

    this.initlist(1)
    setTimeout(()=>{
      refresher.complete(); //停止下拉刷新
      },2000)
    }
    doInfinite(infiniteScroll){//上拉加载
      this.initlist(this.mCurrentPage + 1)
        infiniteScroll.complete();//停止上拉加载
        if(this.stops == 'true'){
          infiniteScroll.enable(false);//停止上拉加载
        }
        // infiniteScroll.enable(false);//停止上拉加载
    }
  // getDict() {//请求数据字典初始化
  //   var that = this;
  //   that.httpService.getDict("HT_REGISTRATION_TYPE", data => {//隐患类型
  //     if (data !== "") {
  //       that.htRegistrationType = data;
  //     }
  //   })
    
  // }
}




